import searchList from'../../components/search/searchlist.vue'
export default{
	data(){
		return{
			isShow:false,
			searchKey:"",
			focus:false,
			searchList:[],
			isSearchList:false,
			searchKeyList:[],//搜索关键词
			pageIndex:1, //当前页数
		}
	},
	components:{
		searchList
	},
	computed:{
		count(){
			return this.$store.state.userName
		}
	},
	onLoad(){
		this.getSearchKyeList();
	},
	methods:{
		//搜索关键词
		getSearchKyeList(){
			const self = this;
			uni.request({
				url:this.$API.URL + this.$API.getKeywords,
				data:{pageIndex:this.pageIndex},
				success(res) {
					console.log(res)
					self.searchKeyList = res.data.data;
					self.pageIndex ++; 
				}
			})
		},
		//根据关键词搜索
		saveKey(key){
			const _this = this;
			uni.hideKeyboard()
			 uni.setStorage({
			     key: 'search_key',
			     data: key,
			     success: function () {
			         _this.$CommonJS.navigateTo('./searchDetail');
			     }
			 });
		},
		searchInput:function(event){
			//console.log(event.detail)
			//清除按钮
			if(event.detail.value!=''){
				this.isShow = true;
				this.searchKey = event.detail.value;
				//this.isSearchList = true;
			}else{
				this.isShow = false;
				this.searchKey = '';
				//this.isSearchList = false;
			}
		},
		clearInput:function(){
			//清空输入框
			this.searchKey = '';
			this.isShow = false;
			this.focus = true;
			//this.isSearchList = false;
		},
		search:function(event){
			const _this = this;
			//搜索
			//console.log(event.detail.value);
			const q = event.detail.value;
			uni.hideKeyboard()
			 uni.setStorage({
			     key: 'search_key',
			     data: q,
			     success: function () {
							
			         _this.$CommonJS.navigateTo('./searchDetail');
			     }
			 });
		
			
		}
	}
}
